import React, { useState } from 'react';
import * as axios from 'axios';
import {
  Row,
  Card,
  CardTitle,
  Form,
  FormGroup,
  Label,
  Input,
  Button,
  Col,
  UncontrolledAlert,
  FormFeedback,
} from 'reactstrap';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { registerUser } from '../../redux/actions';

import IntlMessages from '../../helpers/IntlMessages';
import { Colxx } from '../../components/common/CustomBootstrap';

// import { invalid } from 'moment';
// import { adminRoot } from '../../constants/defaultValues';

const Register = () => {
  // const [email] = useState('demo@gogo.com');
  // const [password] = useState('gogo123');
  // const [name] = useState('name');
  const [{ name, email, mobile, pass, cpass, shop }, setFormState] = useState({
    name: '',
    email: '',
    mobile: '',
    pass: '',
    cpass: '',
    shop: '',
  });

  const [{ type, visible, msg }, setAlert] = useState({
    type: 'primary',
    visible: false,
    msg: '',
  });
  // const [name] = useState('Sarah Kortney');
  // const [mobile] = useState('9874563210');

  const [
    { nameInv, emailInv, mobileInv, passInv, cpassInv, shopInv },
    validateForm,
  ] = useState({
    nameInv: false,
    emailInv: false,
    mobileInv: false,
    passInv: false,
    cpassInv: false,
    shopInv: false,
  });

  const onUserRegister = () => {
    if (
      name !== '' &&
      email !== '' &&
      pass !== '' &&
      shop !== '' &&
      cpass !== ''
    ) {
      if (
        !nameInv &&
        !emailInv &&
        !mobileInv &&
        !passInv &&
        !cpassInv &&
        !shopInv
      ) {
        if (pass === cpass) {
          const data = { name, email, mobile, pass, shop };
          axios
            .post('https://app.appytie.com/get_token.php', data)
            .then(function (response) {
              if (response.error_code === 0) {
                setAlert({
                  type: 'success',
                  visible: true,
                  msg: response.message,
                });
              } else {
                setAlert({
                  type: 'danger',
                  visible: true,
                  msg: response.message,
                });
              }
            })
            .catch(function (error) {
              setAlert({
                type: 'danger',
                visible: true,
                msg: error.message,
              });
            });
        } else {
          setAlert({
            type: 'danger',
            visible: true,
            msg: 'Passwords do not match !',
          });
        }
      } else {
        setAlert({
          type: 'danger',
          visible: true,
          msg: 'Check if all fields are okay !',
        });
      }
    } else {
      setAlert({
        type: 'danger',
        visible: true,
        msg: 'Please fill out all the fields.',
      });
    }
    // call registerUserAction()
  };

  const onDismiss = () => {
    setAlert((prev) => {
      return { ...prev, visible: !prev.visible };
    });
  };

  const handleNameChange = (e) => {
    setFormState((pre) => {
      return { ...pre, name: e.target.value };
    });
    const input = e.target.value;
    const re = /^[a-zA-Z ]*$/;
    // return re.test(String(email).toLowerCase());
    let res;
    if (input !== '') {
      if (input.match(re)) {
        res = false;
      } else {
        res = true;
      }
    } else {
      res = false;
    }
    validateForm((prev) => {
      return { ...prev, nameInv: res };
    });
  };

  const handleEmailChange = (e) => {
    setFormState((pre) => {
      return { ...pre, email: e.target.value.toLowerCase() };
    });
    const input = e.target.value;
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    // return re.test(String(email).toLowerCase());
    let res;
    if (input !== '') {
      if (input.match(re)) {
        res = false;
      } else {
        res = true;
      }
    } else {
      res = false;
    }
    validateForm((prev) => {
      return { ...prev, emailInv: res };
    });
  };

  const handleMobileChange = (e) => {
    setFormState((pre) => {
      return { ...pre, mobile: e.target.value };
    });
    const re = /^[0-9\b]+$/;
    const input = e.target.value;
    let res;
    if (input !== '') {
      if (input.match(re) && input.length === 10) {
        res = false;
      } else {
        res = true;
      }
    } else {
      res = false;
    }
    validateForm((prev) => {
      return { ...prev, mobileInv: res };
    });
  };

  const handleShopChange = (e) => {
    setFormState((pre) => {
      return { ...pre, shop: e.target.value };
    });
  };

  const handlePassChange = (e) => {
    setFormState((pre) => {
      return { ...pre, pass: e.target.value };
    });
    const input = e.target.value;
    let res;
    if (input !== '') {
      if (input.length > 7) {
        res = false;
      } else {
        res = true;
      }
    } else {
      res = false;
    }
    validateForm((prev) => {
      return { ...prev, passInv: res };
    });
  };

  const handleCpassChange = (e) => {
    setFormState((pre) => {
      return { ...pre, cpass: e.target.value };
    });
    const input = e.target.value;
    let res;
    if (input !== '') {
      if (input.length > 7) {
        res = false;
      } else {
        res = true;
      }
    } else {
      res = false;
    }
    validateForm((prev) => {
      return { ...prev, cpassInv: res };
    });
  };

  return (
    <Row className="h-100">
      <Colxx xxs="12" md="10" className="mx-auto my-auto">
        <Card className="auth-card">
          <div className="position-relative image-side ">
            <p className="text-white h2">MAGIC IS IN THE DETAILS</p>
            <p className="white mb-0">
              Please use this form to register. <br />
              If you are a member, please{' '}
              <NavLink to="/user/login" className="white">
                login
              </NavLink>
              .
            </p>
          </div>
          <div className="form-side">
            <NavLink to="/" className="white">
              <span className="logo-single" />
            </NavLink>
            <CardTitle className="mb-4">
              <IntlMessages id="user.register" />
            </CardTitle>
            <Form>
              <Row>
                <Col>
                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>
                      <IntlMessages id="user.fullname" />
                    </Label>
                    <Input
                      type="name"
                      onChange={handleNameChange}
                      value={name}
                      invalid={nameInv}
                    />
                    <FormFeedback>
                      This characters are not allowed!
                    </FormFeedback>
                  </FormGroup>

                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>
                      <IntlMessages id="user.email" />
                    </Label>
                    <Input
                      type="email"
                      value={email}
                      onChange={handleEmailChange}
                      invalid={emailInv}
                    />
                    <FormFeedback>Enter a valid email!</FormFeedback>
                  </FormGroup>

                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>Mobile</Label>
                    <Input
                      type="text"
                      value={mobile}
                      onChange={handleMobileChange}
                      invalid={mobileInv}
                      required
                    />
                    <FormFeedback>Enter a valid mobile no!</FormFeedback>
                  </FormGroup>
                </Col>
                <Col>
                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>Shopify Store Domain</Label>
                    <Input
                      type="text"
                      value={shop}
                      onChange={handleShopChange}
                      invalid={shopInv}
                    />
                  </FormGroup>
                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>Create Password</Label>
                    <Input
                      type="password"
                      value={pass}
                      onChange={handlePassChange}
                      invalid={passInv}
                    />
                    <FormFeedback>
                      Password length must be at least 8!
                    </FormFeedback>
                  </FormGroup>
                  <FormGroup className="form-group has-float-label  mb-4">
                    <Label>Confirm Password</Label>
                    <Input
                      type="password"
                      value={cpass}
                      onChange={handleCpassChange}
                      invalid={cpassInv}
                    />
                    <FormFeedback>
                      Password length must be at least 8!
                    </FormFeedback>
                  </FormGroup>
                  <div className="d-flex justify-content-end align-items-center">
                    <Button
                      color="primary"
                      className="btn-shadow"
                      size="lg"
                      onClick={() => onUserRegister()}
                    >
                      <IntlMessages id="user.register-button" />
                    </Button>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col>
                  <UncontrolledAlert
                    color={type}
                    isOpen={visible}
                    className="mt-4"
                    toggle={onDismiss}
                  >
                    {msg}
                  </UncontrolledAlert>
                </Col>
              </Row>
            </Form>
          </div>
        </Card>
      </Colxx>
    </Row>
  );
};
const mapStateToProps = () => {};

export default connect(mapStateToProps, {
  registerUserAction: registerUser,
})(Register);
